/*------------------------------------NavBar--------------------------------------*/
.navbar {
  display: flex;
  justify-content: space-between;
  background-color: #2b2b2b; 
  border-bottom: 2px solid #444; 
  align-items: center;
  height: 80px;
}

.site-head {
  margin-top: 10px;
  font-size: 24px;
  font-family: 'Georgia', serif; 
  color: #dcdcdc;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 15px;
}

.nav-buttons {
  margin-top: 18px;
  color: #b3b3b3; 
  background-color: transparent;
  border: none;
  font-size: 15px;
  cursor: pointer;
  transition: 0.5s ease;
}

.nav-buttons:hover {
  color: #cca028; 
  transform: scale(1.2);
}

.contact-info {
  margin-top: 18px;
  display: flex;
  gap: 15px;
  margin-right: 10px;
  color: #dcdcdc;
}

.social-icons {
  display: flex;
  gap: 5px;
  color: #dcdcdc;
}

.ph-number {
  font-size: 15px;
}

.mobile-menu-icon {
  display: none;
  cursor: pointer;
}

.nav-links-mobile {
  display: none;
}

@media screen and (max-width: 768px) {
  .nav-links {
    display: none;
    list-style: none;
  }

  .mobile-menu-icon {
    display: block;
  }

  .nav-links-mobile {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 80px;
    right: 0;
    width: 100%;
    background-color: #2b2b2b;
    padding: 20px;
    border-top: 2px solid #444; 
  }

  .nav-buttons {
    margin: 10px 0;
  }
}

