.carousel-item {
  display: none;
  transition: opacity 1s ease-in-out;
  opacity: 0;
  height: 600px;
}

.SliderBack {
  background-color: rgb(241, 241, 241);
}

.carousel-item.active {
  display: block;
  opacity: 1;
}

.fade-in {
  animation: fadeIn 1s;
}

.fade-out {
  animation: fadeOut 1s;
}

@keyframes fadeIn {
  from {
    opacity: 0.5;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0.5;
  }
}

.animate {
  animation: fadeIn 0.5s ease-in-out forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateX(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.WebSlide {
  display: grid;
  grid-template-columns: 45% 50%;
  background-color: rgb(36, 91, 136);
  min-height: 600px; /* Ensure consistent minimum height */
  transition: transform 1s ease-out;
}

.WebSlideSubHead {
  font-family: 'Courier New', Courier, monospace;
}

.WebSliderPara {
  text-align: justify;
}

.HeaderContainer:hover .ImWebSlide {
  transform: scale(1.1);
}

.WebSlideItemLeft {
  color: aliceblue;
  margin-top: 150px;
  display: grid;
  grid-template-rows: 12% 10% 80%;
  margin-left: 150px;
  justify-content: center;
  transition: transform 1s ease-out;
}

.ImWebSlide:hover {
  transform: scale(1.1);
}

.ImWebSlide {
  max-height: 500px; 
  width: auto; 
  transition: 0.5s ease-out;
}

.WebSlideItemNext {
  display: flex;
  align-items: center;
  place-content: center;
  height: 100%; 
}

.SoftSlide {
  max-height: 500px; 
  object-fit: contain;
  transition: 0.5s ease-out;
}

@media (max-width: 1200px) {
  .WebSlide {
    grid-template-columns: 1fr;
    height: auto;
    min-height: 600px; 
  }

  .WebSlideItemLeft {
    margin-left: 0;
    margin-top: 20px;
    grid-template-rows: auto auto auto;
  }

  .ImWebSlide {
    height: auto;
    width: 100%;
    margin: 0;
  }
}

@media (max-width: 768px) {
  .WebSlide {
    grid-template-columns: 1fr;
    padding: 20px;
    min-height: 600px; 
  }

  .WebSlideItemLeft {
    margin-left: 0;
    margin-top: 20px;
    grid-template-rows: auto auto auto;
  }

  .ImWebSlide {
    max-height: 500px;
    width: 100%;
    margin: 0;
  }

  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    width: 30px;
    height: 30px;
  }
}

@media (max-width: 576px) {
  .WebSlideItemNext{
    display: flex;
    align-items: center;
    place-content: center;
    margin-left: -25px;
  }
  .WebSlide {
    grid-template-columns: 1.2fr;
    padding: 10px;
    min-height: 600px; 
  }

  .WebSlideHead {
    font-weight: 800;
    font-size: larger;
  }

  .WebSlideSubHead,
  WebSlideSubHead {
    font-family: 'Courier New', Courier, monospace;
  }

  .WebSlideItemLeft {
    margin-left: 10px;
    margin-top: 20px;
    margin-right: 10px;
    grid-template-rows: auto auto auto;
  }

  .ImWebSlide {
    max-height: 260px; 
    width: auto;
    margin-left: 30px;
    object-fit: contain;
  }

  .carousel-control-prev-icon,
  .carousel-control-next-icon {
    width: 20px;
    height: 20px;
  }

  .WebSliderPara {
    margin-top: -5px;
