  font-family: 'Poppins', sans-serif;
  color: #e0e0e0;
  margin-bottom: 20px;
}

.input-box {
  position: relative;
  margin-bottom: 20px;
}

.input-box input,
.input-box textarea {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  color: #333;
  border: none;
  border-bottom: 2px solid #ca9400;
  background-color: transparent;
  outline: none;
  resize: none;
  transition: 0.3s ease;
}

.input-box textarea {
  height: 100px;
}

.input-box label {
  position: absolute;
  top: 10px;
  left: 10px;
  color: #888;
  pointer-events: none;
  transition: 0.3s ease;
}

.input-box input:focus ~ label,
.input-box input:valid ~ label,
.input-box textarea:focus ~ label,
.input-box textarea:valid ~ label {
  top: -20px;
  left: 0;
  color: #ca9400;
  font-size: 12px;
}

.submit-btn {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: #ca9400;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-btn:hover {
  background-color: #b77900;
}



